/*This two classes are for the text that appear next text the 3d image*/
.textmove{
    font-size: 125px;
    text-decoration: wavy;
    position: absolute;
    top: 300px;
    color: white;
    animation: MoveText  4.5s ease-out forwards  ;
    animation-delay: 0.2;
}

.textmove2{
    font-size: 125px;
    text-decoration: wavy;
    position: absolute;
    top: 300px;
    color: white;
    animation: MoveText2  4.5s ease-out forwards  ;
    animation-delay: 0.2s;

}
/*This two classes are for the text that appear next text the 3d image*/

.permanent-marker-regular {
    font-family: "Permanent Marker", cursive;
    font-weight: 400;
    font-style: normal;
  }
  

  .textmove, .textmove2{
    font-family: "Permanent Marker", cursive;
  }

@keyframes MoveText {
    from{ left: -14%; }
    to{ left: 14%;}
/*For mf text */
}

@keyframes MoveText2{
    from{ left: 120%; }
    to{ left: 69.8%;}
/*For Doom text*/
}
/*THIS WHOLE SECTION IS FOR THE TEXT THAT APPERS NEXT TO THE 3D IMAGE  */


