/*=========Madvillian==========*/
.madivllian-container{
    margin-top: 50%;
    margin-left: 64%;
    background-color: yellow;
    width: 480px;
    height: 450px;
    text-align: center;
    transform-style: preserve-3d;
    transition: 1.5s;
    transform: perspective(1000px) rotateY(0deg);
    border-radius:20px ;
    padding: 10px;
}

.madivllian-container p{
    font-size: 15px;
    color: rgb(95, 85, 85);
}

.madivllian-container img{
  

       position: relative;
       right:  740px;
        margin-top: -80%;
       transform-origin: center ;
       transform-style: preserve-3d;
       transform:rotateY(calc(var(--i)* 420deg)) translateZ(-180px) ;
       -webkit-box-reflect: below 0px linear-gradient(transparent, #0004);
       animation: Moveimg infinite 7.5s ease-out both  ;
       transition: 1.9s ease-in;
      
    }

   
 

    @keyframes Moveimg{
        0%{ top: -10%; }
        
        50%{ 
            top: -4.8%;
            width: 500px;
            height: 500px;
        }
        100%{top: -10%;}
    }
    /*=========Madvillian==========*/

